.testimonial-card__image-wrap:before {
  content: "";
  z-index: 1;
  transform-origin: 100% 100%;
  background-color: #2060f6;
  border-radius: 12px;
  width: 44px;
  height: 109px;
  transition: all .3s;
  position: absolute;
  top: -30px;
  left: -30px;
  transform: rotate(30deg);
}

.testimonial-card__image-wrap:after {
  content: "";
  z-index: 1;
  transform-origin: 0 0;
  background-color: #2060f6;
  border-radius: 12px;
  width: 44px;
  height: 109px;
  transition: all .3s;
  position: absolute;
  bottom: -30px;
  right: -30px;
  transform: rotate(30deg);
}

.testimonial-card:hover {
